import React, {useRef, useState, useEffect} from "react";
import styled from "styled-components";

import Button from "../Button/Button.js";
import Icon from "../Icons/Icon.js";

const SliderTopLevel = styled.div`
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
    ${props => props.spacing &&
        `padding: var(--su-${props.spacing});`
    }
`;
const SliderChild = styled.div`
    width: ${props => props.spacing ? props.width - 2 * parseInt(props.spacing) : props.width}px;
    height: auto;
`;
const SliderContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(${props => props.slides}, max-content);
    ${props => props.spacing
        ? `width: ${((props.width - 2 * parseInt(props.spacing)) * props.slides + props.spacing * (props.slides - 1))}px;`
        : `width: ${props.width * props.slides}px;`
    }
    ${props => props.spacing
        ? `grid-gap: var(--su-${props.spacing});
            transform: translateX(-${props.currentSlide * (props.width - parseInt(props.spacing))}px);`
        : `transform: translateX(-${props.currentSlide * props.width}px);`
    }
    height: auto;
    transition: transform ${props => props.duration}ms ${props => props.easing};
`;
const SliderOverlayControls = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
`;
const SliderOverlayButton = styled(Button)`
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.75);
    border: 1px solid rgba(0, 0, 0, 0.75);
    padding-top: var(--su-20);
    padding-bottom: var(--su-20);

    &:hover {
        box-shadow: none;
        background: var(--brand);
        border-color: var(--brand);
    }

    &:nth-of-type(1) {
        left: 0;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
    &:nth-of-type(2) {
        right: 0;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }
`;
const SliderExternalControls = styled.div`
    display: grid;
    grid-template-columns: max-content max-content max-content;
    justify-content: center;
    padding-top: var(--su-10);
`;
const SliderExternalButton = styled(Button)`
    border: 0;
    background: transparent;

    &:hover {
        background: var(--gray-1);
        box-shadow: none;
    }
`;
const SliderSelector = styled.div`
    ${props => props.controls === "overlay" &&
        `position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);`
    }
    display: grid;
    grid-auto-flow: column;
    grid-gap: var(--su-5);
    background: ${props => props.controls === "overlay" ? `rgba(0, 0, 0, 0.75)` : `none`};
    padding: var(--su-15);
    border-radius: var(--radius-inner);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
`;
const SliderSelectorBullet = styled.div`
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background: ${props => props.controls === "overlay" ? `var(--gray-0)` : `var(--gray-5)`};
    ${props => props.active === true ? `transform: scale(1);` : `transform: scale(0.6);`}
    transition: transform 0.3s ease-in;
`;

export default function Slider(props) {
    const [currentSlide, setCurrentSlide] = useState(0);
    const [width, setWidth] = useState(null);
    const sliderRef = useRef();
    const slides = props.children.length;
    const controlsType = props.controls || "overlay";

    // Update width on window resize
    useEffect(() => {
        function handleResize() {
            const {offsetWidth} = sliderRef.current;
            setWidth(offsetWidth);
            console.log(width);
        }
        handleResize();
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);

    }, [sliderRef]);

    // Auto play
    useEffect(() => {
        if (props.autoPlay) {
            const interval = props.interval || 2000;
            const timer = setInterval(function() {
                currentSlide != slides - 1 ? setCurrentSlide(currentSlide + 1) : setCurrentSlide(0);
            }, interval);
            return () => clearInterval(timer);
        }
    }, [currentSlide]);

    // Change slides on click
    function handleNextSlide(totalSlides) {
        if(currentSlide === totalSlides - 1) setCurrentSlide(0);
        else setCurrentSlide(currentSlide + 1);
    }
    function handlePrevSlide(totalSlides) {
        if(currentSlide === 0) setCurrentSlide(totalSlides - 1);
        else setCurrentSlide(currentSlide - 1);
    }

    return (
        <SliderTopLevel ref={sliderRef} spacing={props.spacing}>
            <SliderContainer spacing={props.spacing} width={width} slides={slides} currentSlide={currentSlide} easing={props.easing || "ease-in"} duration={props.duration || "1000"}>
                {props.children.map((child, index) => {
                    { return <SliderChild key={index} width={width} spacing={props.spacing}>{child}</SliderChild> }
                })}
            </SliderContainer>
            {controlsType === "overlay" &&
                <SliderOverlayControls>
                    <SliderSelector controls={controlsType}>
                        {Array.apply(null, {length: slides}).map((e, index) => (
                            <SliderSelectorBullet controls={controlsType} active={index === currentSlide} key={index} />
                        ))}
                    </SliderSelector>
                    <SliderOverlayButton spacing={props.spacing} onClick={() => handlePrevSlide(slides)}><Icon type="arrow-left" color="--gray-0" /></SliderOverlayButton>
                    <SliderOverlayButton spacing={props.spacing} onClick={() => handleNextSlide(slides)}><Icon type="arrow-right" color="--gray-0" /></SliderOverlayButton>
                </SliderOverlayControls>
            }
            {controlsType === "external" &&
                <SliderExternalControls>
                    <SliderExternalButton spacing={props.spacing} onClick={() => handlePrevSlide(slides)}><Icon type="arrow-left" /></SliderExternalButton>
                    <SliderSelector controls={controlsType}>
                        {Array.apply(null, {length: slides}).map((e, index) => (
                            <SliderSelectorBullet controls={controlsType} active={index === currentSlide} key={index} />
                        ))}
                    </SliderSelector>
                    <SliderExternalButton spacing={props.spacing} onClick={() => handleNextSlide(slides)}><Icon type="arrow-right" /></SliderExternalButton>
                </SliderExternalControls>
            }
        </SliderTopLevel>
    )
}